import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select, DatePicker } from 'antd';
import moment from 'moment';

const { Option } = Select;
const { TextArea } = Input;

const dateFormat = 'YYYY/MM/DD';

// import { scholarshipRatings } from 'dump/data';

// const handleInitialValue = initialValue => {
//   const initialValueDefault = {
//     id: 0,
//     system: '',
//     nameRating: '',
//     codeRating: '',
//     min10: 0,
//     min4: 0,
//     minTrainingPoints: 0,
//     totalMoney: 0,
//   };
//   return { ...initialValueDefault, ...initialValue };
// };


@Form.create({ name: 'formDropoutList' })
class FormDropoutListPage extends Component {
  state = {
    // scholarshipRatingsId: false,
    initialValue: {
      continew: 'Học tiếp'
    },
  };

  componentDidMount = () => {
    // const { match } = this.props;
    // if (match.params.id) {
    //   this.setState({ scholarshipRatingsId: match.params.id });
    //   this.getScholarshipRatins(match.params.id);
    // }
  };

  // getScholarshipRatins = code => {
  //   // const scholarshipMap = scholarshipRatings.filter(object => object.codeRating === code)[0];
  //   const initialValue = handleInitialValue(scholarshipMap);
  //   this.setState({ initialValue });
  // };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    // const { scholarshipRatingsId } = this.state;
    return (
      <Card title='Nhập xét học tiếp'>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Học kì" {...formItemLayout}>
              {getFieldDecorator('system', {
                initialValue: initialValue.system,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Học kì 1</Option>
                  <Option value="2">Học kì 2</Option>
                  <Option value="3">Học kì 3</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số quyết định" hasFeedback>
              {getFieldDecorator('nameRating', {
                initialValue: initialValue.nameRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Năm học" hasFeedback>
              {getFieldDecorator('codeRating', {
                initialValue: initialValue.codeRating,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">2015</Option>
                  <Option value="2">2016</Option>
                  <Option value="3">2017</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Ngày quyết định" hasFeedback>
              {getFieldDecorator('min10', {
                initialValue: initialValue.min10,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Fragment>
                  <DatePicker defaultValue={moment('2015/01/01', dateFormat)} format={dateFormat} />
                </Fragment>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Loại quyết định" hasFeedback>
              {getFieldDecorator('min4', {
                initialValue: initialValue.continew,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select disabled >
                  <Option value="1">1</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nội dung" hasFeedback>
              {getFieldDecorator('minTrainingPoints', {
                initialValue: initialValue.minTrainingPoints,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<TextArea style={{ height: 100 }} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khóa học" hasFeedback>
              {getFieldDecorator('totalMoney', {
                initialValue: initialValue.totalMoney,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Đại học tính chỉ</Option>
                  <Option value="2">Cao đẳng tính chỉ</Option>
                  <Option value="3">Trung cấp tính chỉ</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Lớp học tiếp" hasFeedback>
              {getFieldDecorator('totalMoney', {
                initialValue: initialValue.totalMoney,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select >
                  <Option value="1">Đại học tính chỉ</Option>
                  <Option value="2">Cao đẳng tính chỉ</Option>
                  <Option value="3">Trung cấp tính chỉ</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormDropoutListPage {...props} />
  </Page>
));



